body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-source-serif-pro: "Source Serif Pro";
  --font-source-serif-4: "Source Serif 4";

  /* font sizes */
  --font-size-6xl: 25px;
  --font-size-21xl: 40px;
  --font-size-46xl: 65px;

  /* Colors */
  --color-white: #fff;
  --color-lightsteelblue-100: rgba(153, 184, 229, 0.06);
  --color-darkslateblue: #20406c;
  --color-gray-100: rgba(255, 255, 255, 0.05);
  --color-gray-200: rgba(255, 255, 255, 0);
  --color-gray-300: rgba(255, 255, 255, 0.62);
  --color-steelblue-100: #73a2df;
  --color-steelblue-200: #5286cb;
  --color-steelblue-300: #406594;
  --color-lightskyblue: #84abdc;
  --color-dimgray: #5b5b5b;

  /* Gaps */
  --gap-0: 0px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-xs: 12px;

  /* Border radiuses */
  --br-xl: 20px;
  --br-3xs: 10px;
}
